<template>
  <div>        
    <pulse-loader class="spinner" v-if="false" :loading="isInvoiceUploading"></pulse-loader>
    <CRow v-if="isInvoiceUploadingError">
      <CCol md="12">
        <CAlert show color="danger">Danger Alert</CAlert>
      </CCol>
    </CRow>
    <CRow>
    <CCol md="12">
        <CCard>
          <CCardHeader>
            <strong>Carregar cupom fiscal</strong>
          </CCardHeader>
          <CCardBody>
            <CForm>                            
              <CInputFile
                label="Imagem do cupom fiscal"
                @change="previewFiles"
                horizontal                
              />              
            </CForm>
          </CCardBody>
          <CCardFooter>
            <CButton type="submit" size="sm" color="primary" @click="upload"><CIcon name="cil-check-circle"/> Enviar</CButton>            
          </CCardFooter>
        </CCard>        
      </CCol>
    </CRow>
  </div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import CTableWrapper from './base/Table.vue'

export default {
  name: 'InvoiceUpload',
  components: { CTableWrapper, PulseLoader },
  data() {
      return {
          horizontal: { label:'col-3', input:'col-9' },
          file64 : null
      }
  },
  computed: {
      isInvoiceUploading() { return this.$store.state.invoice.isInvoiceUploading },
      isInvoiceUploadingError() { return this.$store.state.invoice.isInvoiceUploadingError },
  },
  mounted: function() {
    this.file64 = null;
  },
  methods: {        
    previewFiles(event) {
      var self = this;
      console.log(event);
      var reader = new FileReader()
      reader.readAsDataURL(event[0])
      reader.onload = () => {
          self.file64 = reader.result.split('base64,')[1];                    
      }
      reader.onerror = function (error) {          
          self.file64 = null;
      }
    },
    upload(){
      if(this.file64 !== null){
        this.$store.dispatch('invoice/uploadInvoice', this.file64);
      } else {
        alert("Um cupom valido deve ser inserido");
      }
    }
  }
}
</script>
<style>
.spinner {
  display: block;
  position: fixed;
  z-index: 1031; /* High z-index so it is on top of the page */
  top: 50%;
  right: 50%; /* or: left: 50%; */
  margin-top: -..px; /* half of the elements height */
  margin-right: -..px; /* half of the elements widht */
}
</style>